* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root {
  --primaryColor: #af9a7d;
  --mainWhite: #fff;
  --offWhite: #f7f7f7;
  --mainBlack: #222;
  --mainGrey: #ececec;
  --darkGrey: #cfcfcf;
  --mainTransition: all 0.3s linear;
  --mainSpacing: 3px;
  --lightShadow: 2px 5px 3px 0px rgba(0, 0, 0, 0.5);
  --darkShadow: 4px 10px 5px 0px rgba(0, 0, 0, 0.5);
}
/* globals */
body {
  padding-top: 66px;
  color: var(--mainBlack);
  background: var(--mainWhite);
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  line-height: 1.4;
}
h1 {
  font-size: 3em;
  line-height: 1;
  margin-bottom: 0.5em;
}
h2 {
  font-size: 2em;
  margin-bottom: 0.75em;
}
h3 {
  font-size: 1.5em;
  line-height: 1;
  margin-bottom: 1em;
}
h4 {
  font-size: 1.2em;
  line-height: 1.25;
  margin-bottom: 1.25em;
}
h5 {
  font-size: 1em;
  font-weight: bold;
  margin-bottom: 1.5em;
}
h6 {
  font-size: 1em;
  font-weight: bold;
  margin-bottom: 1.5em;
}

.h7 {
  font-size: 11pt;
}

.btn-primary {
  display: inline-block;
  text-decoration: none;
  letter-spacing: var(--mainSpacing);
  color: var(--mainBlack);
  background: var(--primaryColor);
  padding: 0.4rem 0.9rem;
  border: 3px solid var(--primaryColor);
  transition: var(--mainTransition);
  text-transform: uppercase;
  cursor: pointer;
}
.btn-primary:hover {
  background: transparent;
  color: var(--primaryColor);
}
.loading {
  text-transform: capitalize;
  text-align: center;
  margin-top: 3rem;
}
.error {
  text-align: center;
  text-transform: uppercase;
  margin: 2rem 0;
}
.empty-search {
  text-align: center;
  text-transform: capitalize;
  margin: 2rem 0;
  padding: 1rem;
  letter-spacing: var(--mainSpacing);
}
/* end of globals */
/* Navbar */
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 0.75rem 2rem;
  background: var(--offWhite);
  z-index: 1;
}
.nav-header {
  display: flex;
  justify-content: space-between;
}
.nav-btn {
  background: transparent;
  border: none;
  cursor: pointer;
  outline: none;
}
.nav-icon {
  font-size: 1.5rem;
  color: var(--primaryColor);
}
.nav-links {
  height: 0;
  overflow-x: hidden;
  overflow-y: visible; 
  transition: var(--mainTransition);
  list-style-type: none;
}
.nav-links a {
  display: block;
  text-decoration: none;
  padding: 1rem 0;
  color: var(--mainBlack);
  transition: var(--mainTransition);
  text-align: center;
  font-size: 1rem;
  font-weight: 600;
  letter-spacing: var(--mainSpacing);
}
.nav-links a:hover {
  color: var(--primaryColor);
}

.show-nav {
  height: 100px;
}
@media screen and (min-width: 768px) {
  .nav-btn {
    display: none;
  }
  .nav-center {
    max-width: 1170px;
    margin: 0 auto;
    display: flex;
  }
  .nav-links {
    height: auto;
    display: flex;
    margin-left: 4rem;
  }
  .nav-links a {
    margin: 0 1rem;
    padding: 0.5rem 0;
  }
}
/* end of navbar */
/* Hero */
.defaultHero
{
  min-height: 90vh;
  background-size: cover;
  background: url("./images/background.jpg") center/cover no-repeat;
  display: flex;
  align-items: center;
  justify-content: center;
  background-position: center;
  background-repeat: no-repeat;
  transition: var(--mainTransition);
}
.projectsHero {
  min-height: 90vh;
  min-width: 100vw;
  background-size: cover;
  background-image: url("./images/nebulabg.jpg");
  display: flex;
  align-items: center;
  justify-content: center;
  background-repeat: no-repeat;
  background-position: center;
  background-attachment: fixed;
  transition: var(--mainTransition);
}
.libraryHero {
  min-height: 90vh;
  min-width: 100vw;
  background-size: cover;
  background-image: url("./images/library.jpg");
  display: flex;
  align-items: center;
  justify-content: center;
  background-repeat: no-repeat;
  background-position: center;
  transition: var(--mainTransition);
}
.aboutmeHero {
  min-height: 90vh;
  background: url("./images/mountains.jpg");
  display: flex;
  background-size: cover;
  align-items: center;
  justify-content: center;
  background-repeat: no-repeat;
  background-position: center;
  transition: var(--mainTransition);
}
.blogHero {
  min-height: 90vh;
  min-width: 100vw;
  background-size: cover;
  background: var(--primaryColor);
  display: flex;
  align-items: center;
  justify-content: center;
  background-repeat: no-repeat;
  background-position: center;
  transition: var(--mainTransition);
}
/* End of Hero */
/* Banner */
.banner {
  display: inline-block;
  background: rgba(0, 0, 0, 0.5);
  color: var(--mainWhite);
  padding: 2rem 1rem;
  text-align: center;
  letter-spacing: var(--mainSpacing);
}
.banner h1 {
  font-size: 2.5rem;
}
.banner div {
  width: 10rem;
  height: 5px;
  background: var(--primaryColor);
  margin: 1.7rem auto;
}
.banner p {
  font-size: 1.2rem;
  margin-bottom: 2rem;
}
@media screen and (min-width: 576px) {
  .banner {
    padding: 2rem 3rem;
  }
  .banner h1 {
    font-size: 3rem;
  }
}
@media screen and (min-width: 992px) {
  .banner {
    padding: 2rem 6rem;
  }
  .banner h1 {
    font-size: 4rem;
  }
}
/* End of Banner */
/* Title */
.section-title {
  text-align: center;
  margin-bottom: 4rem;
}
.section-title h4 {
  font-size: 2rem;
  letter-spacing: var(--mainSpacing);
  text-transform: capitalize;
  margin-bottom: 1rem;
}
.section-title div {
  width: 5rem;
  height: 5px;
  margin: 0 auto;
  background: var(--primaryColor);
}
/* end of Title */

.blogpost {
  width: 50vw;
}

.blogpost:hover {
  background: #f9f9f9;
}

.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-content {
  position: relative;
  background-color: #f9f9f9;
  min-width: 200px;
  min-height: 40px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  text-decoration: none;
  letter-spacing: var(--mainSpacing);
  color: var(--mainBlack);
  background: var(--primaryColor);
  padding: 0.4rem 0.9rem;
  border: 3px solid var(--primaryColor);
  transition: var(--mainTransition);
  cursor: pointer;
  display: block;
  overflow: hidden;
}

.right {
  float: right;
}

.dropdown-content:hover {
  background: transparent;
  color: var(--primaryColor);
}

.blogbanner {
  display: block;
  background: rgba(0, 0, 0, 0.5);
  color: var(--mainWhite);
  padding: 2rem 1rem;
  text-align: center;
  float: center;
  align-content: center;
  align-items: center;
  align-self: center;
  letter-spacing: var(--mainSpacing);
  width: 100vw;
}

.box {
  width: 500px;
  height: 500px;
  display: flex;
  justify-content: center;
}

.val {
	-webkit-user-select: none; /* Safari */        
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE10+/Edge */
    user-select: none; /* Standard */
    align-content: center;
    align-items: center;
    position: relative;
}

.left {
  float: left;
  font-size: 14pt;
}

.blog {
    position: relative;
    background: rgba(249, 249, 249, 0.5);
    text-align: center;
    align-self: center;
    align-content: center;
    box-align: center;
    float: left;
    height: 150px;
    width: 350px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    color: var(--mainBlack);
    transition: var(--mainTransition); 
    display: block;
    overflow: scroll;
    padding: 20px;
    margin: 40px;
    -moz-transition: var(--mainTransition); /* Firefox 4 */
    -webkit-transition: var(--mainTransition); /* Safari and Chrome */
    -o-transition: var(--mainTransition); /* Opera */
    -ms-transition: var(--mainTransition); /* Explorer 10 */
  }

  .blog:hover {
    background: rgba(0,0,0,0.8);
    text-align: center;
    align-self: center;
    float: center;
    color: var(--primaryColor);
    transition: var(--mainTransition);
    height: 300px;
    -moz-transition: var(--mainTransition); /* Firefox 4 */
    -webkit-transition: var(--mainTransition); /* Safari and Chrome */
    -o-transition: var(--mainTransition); /* Opera */
    -ms-transition: var(--mainTransition); /* Explorer 10 */
    border-block-start: 20px solid var(--primaryColor);
    border-block-end: 20px solid var(--primaryColor);
    padding: 40px;
  }

.ada {
  background-image: url("./images/ada.png");
  width: 100%;
  height: 100%;
  align-self: center;
  align-content: center;
  align-items: center;
  float: left;
  position: relative;
}

.blog-content {
  position: relative;
  background: #f9f9f9;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  text-decoration: none;
  letter-spacing: var(--mainSpacing);
  color: var(--mainBlack);
  padding: 0.4rem 0.9rem;
  border: 3px solid var(--primaryColor);
  align-self: center;
  float: center;
  min-width: 30vw;
  min-height: 20vh;
  transition: ease-in-out;
  
}

.blank {
  background: transparent;
  display: inline;
  transition: var(--mainTransition);
  color: var(--mainBlack);
  font-size: 11pt;
  overflow: scroll;
  color: var(--primaryColor);
}

.blank:hover {
  min-width: 100%;
  min-height: 100%;
  color: white;
}

div::-webkit-scrollbar {
  width: 0px;
  background: transparent;
  color: transparent;
}

input {
  width: 400px;
}


.blackbar {
  width: 50vw;
  align-self: center;
  align-content: center;
  padding: 20px;
  box-align: center;
  display: inline-block;
  border: 1px solid black;
  position: relative;
}

/* projects */
.projects {
  padding: 5rem 0;
}
.projects {
  background: transparent;
  text-align: center;
}
.projects-center {
  width: 90vw;
  height: 50vh;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  grid-row-gap: 1rem;
  grid-column-gap: 30px;
  background: transparent;
}
.project span {
  display: inline-block;
  color: var(--primaryColor);
  font-size: 2.5rem;
  margin-bottom: 1.5rem;
}
.projects h6 {
  letter-spacing: var(--mainSpacing);
}
.projects p {
  width: 80%;
  margin: 0 auto;
}
@media screen and (min-width: 992px) {
  .services-center {
    width: 95vw;
    max-width: 1170px;
  }
}

@media screen and (min-width: 1200px) {
  .services p {
    width: 100%;
  }
}